@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
    body.noScroll{
        @apply overflow-hidden
    }
    .btn--clear{
        @apply pb-2 pt-1.5 text-grayCustom-light hover:text-cyanCustom hover:underline font-semibold text-base
    }
    .btn--close{
        @apply cursor-pointer rounded px-4 py-1 bg-cyanCustom-lightest font-semibold text-base text-grayCustom  hover:underline hover:text-cyanCustom
    }
    .btn--close--x{
        @apply p-2 cursor-pointer text-cyanCustom hover:text-cyanCustom-darkest transition-colors font-bold
    }
    label{
        @apply block text-cyanCustom-darkest mb-2
    }
    .text-input, .select{
        @apply w-full md:w-6/12 pr-4 mb-2
    }
    input[type=text], select{
        @apply w-full h-10 rounded mb-2 border-2 border-gray-300 border-solid px-1.5
    }
    input[type=text].error, select.error{
        @apply border-red-500
    }
    input[type=checkbox]{
        @apply rounded border-2 border-gray-300 border-solid px-1.5 h-6 w-6
    }
    .checkbox-single{
        @apply mr-8 mb-4
    }
    .checkbox-group{
        @apply w-full mb-4
    }
    .checkbox-grouping{
        @apply flex flex-wrap
    }
    .checkbox-grouping > div{
        @apply mr-8 flex 
    }
    .checkbox-grouping input[type=checkbox]{
        @apply mr-2
    }
    .create-form button{
        @apply bg-cyanCustom rounded px-4 py-1 text-cyanCustom-lightest font-semibold text-base mr-1 hover:bg-cyanCustom-darkest transition-colors
    }
}
@layer components {
    .App{
        @apply overflow-x-hidden relative
    }
    header{
        @apply bg-header-mobile bg-cyanCustom lg:bg-header-desktop h-32
    },
    main{
        @apply relative bg-cyanCustom-lightest px-6 pb-4 pt-8 min-h-screen
    },
    /* Job posting */
    .job{
        @apply bg-white max-w-5xl mx-auto flex flex-wrap lg:flex-nowrap justify-between rounded mb-10 lg:mb-5 px-5 lg:px-5 lg:pb-5 lg:pt-4 border-l-4 border-transparent shadow-lgCyan
    },
    .job.featured{
        @apply border-cyanCustom
    },
    .job-details{
        @apply lg:flex text-cyanCustom-darkest font-bold w-full lg:w-auto
    },
    .job-details img{
        @apply -mt-5 w-12 lg:m-0 lg:w-19 lg:h-19 lg:ml-2 lg:mr-5 lg:mt-2
    },
    .job-details--top{
        @apply flex flex-nowrap font-medium text-xs mb-1.5 mt-1.5
    },
    .job-details--btm{
        @apply flex flex-nowrap text-xs text-grayCustom font-normal mt-2.5 lg:mt-1.5 pb-4 lg:pb-2 border-grayCustom-light border-b lg:border-0
    },
    .job-details--btm span{
        @apply px-1.5 text-grayCustom-light
    },
    .job-details--company{
        @apply text-cyanCustom font-bold pt-1 mr-5
    },
    .badge{
        @apply text-white rounded-2xl py-1 px-2 mr-2
    },
    .badge--new{
        @apply bg-cyanCustom
    },
    .badge--featured{
        @apply bg-cyanCustom-darkest
    },
    .tag-container{
        @apply pt-4 mb-3
    },
    .tag-list{
        @apply flex flex-wrap lg:items-center lg:h-full
    },
    .tag{
        @apply cursor-pointer bg-cyanCustom-light text-cyanCustom text-xs font-bold px-2.5 pb-2 pt-1.5 mr-2 mb-2 rounded-md
    },
    .tag-list .tag{
        @apply hover:text-white hover:bg-cyanCustom
    },
    /* Filter List & Selected Tags */
    .filter-container{
        @apply bg-white rounded max-w-5xl mx-auto shadow-lgCyan -top-16 relative flex flex-nowrap justify-between px-5 py-4 lg:px-8 lg:py-4
    }
    .filter-list{
        @apply flex flex-wrap content-center
    }
    .tag-filter-container{
        @apply flex flex-nowrap my-2 text-cyanCustom group cursor-pointer
    }
    .tag--active{
        @apply rounded-r-none mr-0 my-0
    }
    .tag-filter-container span{
        @apply bg-cyanCustom px-2.5 pb-2 pt-1.5 mr-2 rounded-r-md transition-colors
    }
    .tag-filter-container span img{
        @apply mt-0.5
    }
    .group:hover span{
        @apply bg-cyanCustom-darkest
    }
    /* Form */
    .create-form{
        @apply px-6 md:px-14 py-7 fixed overflow-y-scroll w-11/12 md:w-3/6 top-0 bottom-0 -right-full md:-right-2/4 bg-white transition-all shadow bg-cyanCustom-lightest
    }
    .create-form.show{
        @apply right-0
    }
    .create-form h2{
        @apply text-cyanCustom font-bold text-xl
    }
    /* Notification */
    .notification{
        @apply absolute text-white z-50 w-full py-4 px-2 text-base font-semibold text-center
    }
    .notification.success{
        @apply bg-green-500
    }
    .notification.error{
        @apply bg-red-500
    }
}
